* {
    margin: 0;
    padding: 0;
    font-family: Nunito Sans;
    text-decoration: none;

}

.App {
    padding: 0cm 0cm 3cm 0cm;
}

body {
    // background-color: #e9feff;
}


.name {
    font-family: Madimi One;
    font-size: 60px;
    line-height: 1.75cm;

    text-align: center;
    // font-weight: 600;
    color: #08a5a8;
}

.buttons {
    margin-top: .5cm;
