}

.helpline {
    padding: .3cm 1.5cm;
    // width: 100%;
    border-radius: 1000px;
    background-color: #08a5a8;
    border: none;
    color: #e9feff;
    font-size: 16px;
    letter-spacing: .01cm;
    cursor: pointer;
    border: 2.5px solid #08a5a8;
    transition: .3s ease;

}

.cb {
    padding: .3cm 1cm;
    width: calc(100% - 2cm);
    border-radius: 1000px;
    background-color: #08a5a8;
    border: none;
    text-align: center;
    color: #e9feff;
    font-size: 16px;
    letter-spacing: .01cm;
    cursor: pointer;
    border: 2.5px solid #08a5a8;
    transition: .3s ease;
}

.sb:hover {
    background-color: transparent;
    color: black;
}

.ab {
    background-color: transparent;
    color: black;

    &:Hover {
